import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../App.css';
import {Button} from 'react-bootstrap';

class Poster extends React.Component{
  constructor(props)
  { super(props);
    this.state={
    movie:'',
    item:'',
    cursor:0,
    searchItems:[],
    loading :true
  }
  this.movieslist=[]
  this.updatevalue=this.updatevalue.bind(this);
  this.fetchmovie=this.fetchmovie.bind(this);
  this.handlesubmit=this.handlesubmit.bind(this);
  this.autocomplete = this.autocomplete.bind(this);
  this.hanldeKeyup = this.hanldeKeyup.bind(this);
  this.hanldeKeydown = this.hanldeKeydown.bind(this);
  this.handleListKeydown = this.handleListKeydown.bind(this);
  this.selectItem = this.selectItem.bind(this);
  this.handleChange = this.handleChange.bind(this);
  }
  
  autocomplete(evt) {
    this.setState({item:evt.target.value})
    let text = evt.target.value;
    const url="https://api.themoviedb.org/3/search/movie?api_key=d272326e467344029e68e3c4ff0b4059&language=en-US&query="+text;
    fetch(url)
      .then(res => res.json())
      .then(data => {
        this.setState({ searchItems: data.results});
        console.log(this.state.searchItems);
      });
  }

  hanldeKeyup(evt) {
    if (evt.keyCode === 27) {
      this.setState({ searchItems: [] });
      return false;
    }
  }

  hanldeKeydown(evt) {
    const { cursor, searchItems } = this.state;
    console.log(evt.keyCode);

    // arrow up/down button should select next/previous list element
    if (evt.keyCode === 38 && cursor > 0) {
      this.setState(prevState => ({
        cursor: prevState.cursor - 1
      }));
    } else if (evt.keyCode === 40 && cursor < searchItems.length - 1) {
      this.setState(prevState => ({
        cursor: prevState.cursor + 1
      }));
    }
    if (evt.keyCode === 13) {
      if(searchItems[cursor]===undefined){
        window.location.href="/";
      }
      else{
      let currentItem = searchItems[cursor].title;
      if (currentItem !== undefined) {
        this.setState({ item: currentItem, searchItems: [] });
        this.handlesubmit();
      }
    }
  }
    if (evt.keyCode === 8) {
      this.setState({ item:"",searchItems:[]} );
    }
  }

  selectItem(id) {
    console.log(id);
    const { searchItems } = this.state;
    let selectedItem = searchItems.find(item => item.id === id);
    console.log(selectedItem.title);
    this.setState({ item:  selectedItem.title} );
    this.setState({ searchItems: [] });
    this.handlesubmit();
  }

  handleListKeydown(evt) {
    console.log(evt.keyCode);
  }

  handleChange(evt) {
    this.setState({ item: { [evt.target.name]: evt.target.value } });
  }

  updatevalue(e){
    this.setState({
      item:e.target.value
    })
  }
  fetchmovie = async val =>{
    const url="https://api.themoviedb.org/3/search/movie?api_key=d272326e467344029e68e3c4ff0b4059&language=en-US&query="+val;
    const response = await fetch(url);
    const data =await response.json();
    //console.log(JSON.stringify(data));
    this.setState({movie:data.results,loading:false})
    this.movieslist=this.state.movie;
    console.log(this.movieslist);
  }
  handlesubmit(){
  this.fetchmovie(this.state.item);
  }
 
    render(){
      const { searchItems,cursor} = this.state;
        return(
          <div>
            <div className="poster">
                <div className="title_head">
                  <h2>Welcome.</h2>
                  <h3>Millions of movies, TV shows and people to discover. Explore now.</h3>
                </div>
                <div className="search">
              <input
              type="text"
              placeholder="Search for movie,Tv shows ..."
              onChange={this.autocomplete}
              onKeyUp={this.hanldeKeyup}
              onKeyDown={this.hanldeKeydown}
              value={this.state.item}
              className="custom-input form-control"
            />
             <Button id="search-input" type="button" onClick={this.handlesubmit}><b>search</b></Button>

            {searchItems.length > 0 && (
              <ul className="list-group">
                {searchItems.map((item,id) => (
                  <li
                  className={
                    cursor === id ? "active list-group-item" : "list-group-item"
                  }
                    key={id}
                    onClick={() => this.selectItem(item.id)}
                    onKeyDown={evt => this.handleListKeydown(evt,id)}
                  >
                    {item.title}
                  </li>
                ))}
              </ul>
            )}
                
                
              </div>
              <div>

      </div>
            </div>
            <div>
            {this.state.loading || !this.state.movie ? (
                <div>
                  <Falseload/>
                </div>
            ):(
                <div>
        <table class="table">
        <thead>
          <tr>
            <th colSpan="2">Search results..</th>  
          </tr>
        </thead>
        <tbody>
          {this.state.movie.map(todo => (
            <tr key={todo.id}>
              <td>
                <img src={"https://image.tmdb.org/t/p/w500"+todo.poster_path} alt="Poster not available"/>
              </td>
              <td>
              <b>Title:&nbsp;{todo.title}</b>  
              <br/>
              <b>Rating:&nbsp;{todo.vote_average}</b>
              <br/>
              <p style={{"color":"grey"}}>Release Date:{todo.release_date}</p>
              <br/>
              <b style={{"color":"grey","marginBottom":"0px"}}>{todo.overview}</b> 
              </td>               
            </tr>
          ))}
        </tbody>
      </table>
                    </div>
                
            )}
            </div>
          </div>
        )
    }
}

class Falseload extends React.Component{
  render() {
    return (
      <div className="falseload">
        <div class="column_content flex">
          <div class="column">
            <h5>Get access to maintain your own <em>custom personal lists</em>, <em>track what you've seen</em> and search and filter for <em>what to watch next</em>â€”regardless if it's in theatres, on TV or available on popular streaming services like Netflix, Amazon Prime Video, and Hotstar.</h5>
            <Button variant="primary">Sign Up</Button>
          </div>

          <div class="column">
            <ul>
              <li>Enjoy TMDb ad free</li>
              <li>Maintain a personal watchlist</li>
              <li>Filter by your subscribed streaming services and find something to watch</li>
              <li>Log the movies and TV shows you've seen</li>
              <li>Build custom lists</li>
              <li>Contribute to and improve our database</li>
            </ul>
          </div>

        </div>
      </div>
    );
  }
}
export default Poster;